.bold-text-wrapper {
  position: relative;
  top: -1px;
  left: -1px;

  .bold-text-shadow {
    position: relative;
    top: 5px;
    left: 5px;
    font-size: 4.6rem;
    font-weight: 400;
    color: #4c49435b;
    -webkit-text-stroke-width: 16px;
    -webkit-text-stroke-color: #4c49435b;
  }

  .bold-text-bg {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    font-size: 4.6rem;
    font-weight: 400;
    color: #4c4943;
    -webkit-text-stroke-width: 16px;
    -webkit-text-stroke-color: #4c4943;
  }

  .bold-text {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    font-size: 4.6rem;
    font-weight: 400;
    color: #fff6e2;
  }
}
