.card-wrapper {
  position: relative;
  overflow: visible;
  transition: all 0.2s ease-in-out;
  text-align: center;

  &:hover {
    transform: scale(1.2);
    z-index: 100;
    transition: all 0.05s ease-in-out;

    &::before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: calc(100% - 60px);
      height: calc(100% - 60px);
      margin: 30px;
      background-color: rgba(0, 0, 0, 0.5);
      z-index: -1;
      box-shadow: 0px 0px 45px 31px rgba(0, 0, 0, 0.75);
    }
  }

  .card {
    position: relative;

    .color-keyword {
      color: #efc851 !important;
    }

    .color-positive {
      color: #7fff00 !important;
    }

    .color-negative {
      // Use the same color as positive because this is used in card upgrades,
      // where a negative number may not be bad.
      color: #7fff00 !important;
      // color: #fa6361 !important;
    }

    .card-background {
      position: absolute;
      top: 40px;
      left: 40px;

      .bg_custom-1024 {
        width: 607px;
        height: 838px;
        background-repeat: no-repeat;
        background-position: -207px -95px;
      }
    }

    .card-energy {
      position: absolute;
      top: 0;
      left: 0;

      .card-energy-sprite {
        position: relative;
        display: flex;
        align-items: center;
        justify-content: center;
      }

      .card_custom_orb-1024 {
        width: 164px;
        height: 164px;
        background-repeat: no-repeat;
        background-position: 0 0;
      }
    }

    .card-image {
      position: absolute;
      top: 128px;
      left: 96px;

      .image_custom-1024 {
        width: 500px;
        height: 380px;
        background-repeat: no-repeat;
        background-position: 0 0;
      }
    }

    .card-frame {
      position: absolute;

      &.attack {
        top: 158px;
        left: 82px;

        .card-frame-sprite span {
          position: absolute;
          bottom: 10px;
          left: 0;
          right: 0;
        }
      }

      &.skill {
        top: 156px;
        left: 84px;

        .card-frame-sprite span {
          position: absolute;
          bottom: 5px;
          left: 0;
          right: 0;
        }
      }

      &.power {
        top: 48px;
        left: 79px;

        .card-frame-sprite span {
          position: absolute;
          bottom: 6px;
          left: 0;
          right: 0;
        }
      }

      .card-frame-sprite {
        position: relative;

        span {
          color: #595959;
          font-size: 2rem;
          font-weight: 700;
          text-shadow: 4px 4px #3636363f, 0px 0px 1px #595959;
        }
      }
    }

    .card-banner {
      position: absolute;
      top: 75px;
      left: 20px;

      .card-banner-sprite {
        position: relative;
      }
    }

    .card-text {
      position: absolute;
      top: 520px;
      left: 120px;
      width: 450px;
      height: 300px;
      display: flex;
      align-items: center;
      justify-content: center;

      span {
        width: 100%;
        text-align: center;
        color: #fff6e2;
        font-size: 3.1rem;
        font-weight: 400;
        line-height: 3.25rem;
        text-shadow: 4px 4px 2px #00000065;
      }

      .energy-icon {
        width: 42px;
        height: 42px;
        display: inline-block;
        position: relative;
      }

      .energy-orb-sprite {
        display: inline-block;
        vertical-align: middle;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%) scale(2, 2);
      }
    }
  }

  .keywords-list {
    display: none;
    position: absolute;
    left: calc(100% - 10px);
    top: calc(100% - 300px);
    z-index: 10;
    gap: 0.15rem;
    flex-direction: column;

    &.left {
      right: calc(100% - 10px);
      left: auto;
    }

    @media screen and (max-width: 700px) {
      left: 0;
      top: calc(100%);

      &.left {
        left: 0;
        top: calc(100%);
      }
    }
  }

  &:hover .keywords-list {
    display: flex;
  }
}
