.regular-text-wrapper {
  position: relative;
  top: 13px;
  left: 3px;

  .regular-text-shadow {
    position: relative;
    top: 7px;
    left: 7px;
    font-size: 2.9rem;
    font-weight: 400;
    color: #59564f5b;
    -webkit-text-stroke-width: 10px;
    -webkit-text-stroke-color: #59564f5b;
    letter-spacing: 3px;
  }

  .regular-text-bg {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    font-size: 2.9rem;
    font-weight: 400;
    color: #59564f;
    -webkit-text-stroke-width: 10px;
    -webkit-text-stroke-color: #59564f;
    letter-spacing: 3px;
  }

  .regular-text {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    font-size: 2.9rem;
    font-weight: 400;
    color: #fff6e2;
    letter-spacing: 3px;
  }
}
