.loading {
  font-size: 2rem;
  font-weight: 700;
  color: #595959;
  text-shadow: 4px 4px #3636363f, 0px 0px 1px #595959;
}

.characters-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  margin-bottom: 20px;

  span {
    cursor: pointer;
    color: #fff6e2;
    font-size: 3rem;
    text-shadow: 2px 2px 2px rgb(0, 0, 0), 0px 0px 4px #000000;
  }
}

.sorting-bar {
  width: 100%;
  margin-bottom: 2rem;
  background-color: gray;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center;
  border-radius: 5px;

  button {
    width: 120px;
    height: 40px;
    background-color: transparent;
    font: inherit;
    border: none;
    outline: none;
    cursor: pointer;
    color: #fff6e2;
    font-size: 1.5rem;
    text-shadow: 2px 2px 2px rgb(0, 0, 0), 0px 0px 4px #000000;
  }
}

.cards-list {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
}
