.keyword {
  position: relative;
  width: 256px;

  .keyword-bg {
    position: absolute;
    z-index: -1;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: grid;
    grid-template-rows: min-content 1fr min-content;

    .keyword-top {
      background-image: url('sprites/tipTop.png');
      background-size: 100% 100%;
      width: 256px;
      height: 25.6px;
    }
    .keyword-mid {
      background-image: url('sprites/tipMid.png');
      background-repeat: no-repeat;
      background-size: 100% 100%;

      width: 256px;
      height: calc(100% + 4px);
      // use a 2px border to avoid gaps
      margin-top: -2px;
      margin-bottom: -2px;
    }
    .keyword-bot {
      background-image: url('sprites/tipBot.png');
      background-size: 100% 100%;
      width: 256px;
      height: 25.6px;
    }
  }

  .keyword-content {
    width: 100%;
    padding-top: 10px;
    padding-left: 18px;
    padding-right: 24px;
    padding-bottom: 11px;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    text-align: left;

    .keyword-title {
      color: #efc851;
      text-transform: capitalize;
      font-size: 1rem;
      font-weight: 400;
      text-shadow: 1.5px 1.5px 1px #000000a1;
    }

    .keyword-description {
      color: #fff7de;
      font-size: 1rem;
      font-weight: 300;
      text-shadow: 1.5px 1.5px 1px #000000a1;

      .color-b {
        color: #87cdea !important;
      }

      .color-y {
        color: #efc851 !important;
      }

      .color-g {
        color: #7fff00 !important;
      }

      .color-r {
        color: #fa6361 !important;
      }
    }
  }
}
